              <div class="cp_navigation-button" v-for="(section, index) in sections" :key="index" @click="scrollToSection(index)" :class="{'active': currentSection === index}">
                <div class="button-icon">
                  <span v-if="hoveredSection === index"></span> <!-- 화살표를 표시 -->
